body {
    font-family: 'Arial', sans-serif;
    /* background: linear-gradient(rgb(141, 96, 96),rgb(108, 108, 156)); */
    background-repeat: no-repeat;
    background-color: rgb(175, 166, 176);

  }
  
  .app {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 20px;

  }
  
  .planet-card {
    border: 5px solid #827b7b;
    padding: 10px;
    margin: 10px;
    width: 300px;
    border-radius: 20px;
    padding-left: 30px;
  }
  
  .resident-list {
    margin-top: 10px;
  }
  
  .pagination {
    margin-top: 130px;
    display: flex;
    justify-content: space-between;
    gap: 10px;
  }
  
  button {
    padding: 5px 10px;
    cursor: pointer;
    width: 150px;
    height: 50px;
    border-radius: 30px;
    background-color:azure;
    font-size: 18px;
    font-family: 'Times New Roman', Times, serif;
    font-weight: bold;
  }
  span{
    margin-top: 15px;
  }
  
  button:hover{
    background-color: rgb(175, 166, 176);
  }